#page-map {
    width: 100vw;
    height: 100vh;

    position: relative;
    display: flex;
}

#page-map aside {
    width: 440px;
    background: linear-gradient(329.54deg, #29B6D1 0%, #00C7C7 100%);
    padding: 80px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

#page-map aside h2 {
    font-size: 40px;
    font-weight: 800;
    line-height: 42px;
    margin-top: 64px;
}
#page-map aside p {
    line-height: 28px;
    margin-top: 24px;   
}

#page-map asidie footer {
    display: flex;
    flex-direction: column;

    line-height: 24px;
}

#page-map a side footer strong {
    font-weight: 800;
}